import React from "react";
import logo from "../images/logo.svg";
import {Link} from "gatsby";

const Header = () => {
    return (
        <header>
            <title>Ассоциация лицеистов IT-лицея КФУ</title>
            <Link to="/" style={{color: 'inherit'}}>
                <div className="Header__Container">
                    <img style={{height: '4em'}} src={logo} alt="Эмблема Ассоциации лицеистов"/>
                    <p className="Header__Desktop">
                        Орган ученического со-управления «Ассоциация лицеистов» <br/>
                        СУНЦ &mdash; ОШИ «IТ-лицей» ФГАОУ ВО <br/>
                        «Казанский (Приволжский) федеральный университет»
                    </p>
                </div>
            </Link>
            <hr/>
            <br/>
        </header>
    );
};

export default Header;