import * as React from "react"
import "../styles.scss"
import Header from "./header";
import {DepTreeElement, DocLink, UserLink} from "../utils";
import {Link} from "gatsby";
import Footer from "./footer";

const IndexPage = ({pageContext: {departments, members, docs, heads}}) => {
    return (
        <>
            <Header/>
            <main>

                <h2>Структура</h2>
                <ul>
                    {departments.map(dep => (<DepTreeElement key={`dep_${dep.id}`} dep={dep}/>))}
                </ul>
                <br/>
                <h2>Руководство</h2>
                <ul>
                    <li>
                        Президент IT-лицея КФУ &mdash; {heads.president !== null ?
                        <UserLink user={members.find(m => m.id === heads.president)}/> : "не назначен"}
                        {heads.interims.president !== null && <ul>
                            <li>Временно исполняющий обязанности (до {heads.interims.president_date}) &mdash; <UserLink
                                user={members.find(m => m.id === heads.interims.president)}/>
                            </li>
                        </ul>}
                    </li>
                    <li>Премьер-министр &mdash; {heads.premier_minister !== null ?
                        <UserLink user={members.find(m => m.id === heads.premier_minister)}/> : "не назначен"}
                        {heads.interims.premier_minister !== null && <ul>
                            <li>Временно исполняющий обязанности (до {heads.interims.premier_minister_date}) &mdash;
                                <UserLink
                                    user={members.find(m => m.id === heads.interims.premier_minister)}/>
                            </li>
                        </ul>}
                    </li>
                    <li>Министр развития проектов Ассоциации лицеистов &mdash; {heads.minprojects !== null ?
                        <UserLink user={members.find(m => m.id === heads.minprojects)}/> : "не назначен"}
                        {heads.interims.minprojects !== null && <ul>
                            <li>Временно исполняющий обязанности (до {heads.interims.minprojects_date}) &mdash;
                                <UserLink
                                    user={members.find(m => m.id === heads.interims.minprojects)}/>
                            </li>
                        </ul>}
                    </li>
                    <li>Министр стратегических инициатив по развитию новых
                        проектов &mdash; {heads.mininitiatives !== null ?
                            <UserLink user={members.find(m => m.id === heads.mininitiatives)}/> : "не назначен"}
                        {heads.interims.mininitiatives !== null && <ul>
                            <li>Временно исполняющий обязанности (до {heads.interims.mininitiatives_date}) &mdash;
                                <UserLink
                                    user={members.find(m => m.id === heads.interims.mininitiatives)}/>
                            </li>
                        </ul>}
                    </li>
                    <li>Министр спорта, киберспорта, культуры и творчества &mdash; {heads.minculture !== null ?
                        <UserLink user={members.find(m => m.id === heads.minculture)}/> : "не назначен"}
                        {heads.interims.minculture !== null && <ul>
                            <li>Временно исполняющий обязанности (до {heads.interims.minculture_date}) &mdash; <UserLink
                                user={members.find(m => m.id === heads.interims.minculture)}/>
                            </li>
                        </ul>}
                    </li>
                </ul>
                <br/>
                <h2>Основные документы</h2>
                <ul>
                    {docs.filter(doc => doc.type === 'main').map(doc => (
                        <li key={`doc_${doc.id}`}><DocLink doc={doc}/></li>
                    ))}
                </ul>
                <br/>
                <h2>Остальные разделы</h2>
                <ul>
                    <li><Link to="/docs">Все документы</Link></li>
                    <li><Link to="/members">Полный состав</Link></li>
                    <li><Link to="/polls">Опросы</Link></li>
                </ul>
            </main>
            <Footer/>
        </>
    )
}

export default IndexPage
