import React from "react";
import {graphql, useStaticQuery} from "gatsby";

const Footer = () => {
    const data = useStaticQuery(graphql`
      query {
        currentBuildDate {
          currentDate
        }
      }
    `)
    console.log(data);

    return (
        <footer>
            <br/>
            <hr/>
            <br/>
            <p>
                Орган ученического со-управления «Ассоциация лицеистов», 2020-{new Date().getFullYear()} г. <br/>
                Шефствующая организация: Cпециализированный учебный научный центр &mdash; общеобразовательная
                школа-интернат «IТ-лицей» ФГАОУ ВО «Казанский (Приволжский) федеральный университет»<br/><br/>
                Последнее обновление: {data.currentBuildDate.currentDate}</p>
        </footer>
    );
};

export default Footer;