import React from "react";
import {Link} from "gatsby";

const UserLink = ({user}) => {
    if (!user) return null;
    return (<Link to={`/members/${user.id}`}
                  className={user.kicked ? "kicked" : ""}>{user.name}, {user.group} {user.position && `(${user.position})`}</Link>)
}
const DepLink = ({dep}) => {
    return (<Link to={`/deps/${dep.id}`}>{dep.name} {dep.position && `(${dep.position})`}</Link>)
}
const DocLink = ({doc}) => {
    return (<a target="_blank" rel="noreferrer" href={doc.link}>{doc.name} (№ {doc.id} от {doc.date})</a>)
}
const PollLink = ({poll}) => {
    return (
        <>
            {!poll.locked && <a target="_blank" rel="noreferrer" href={poll.link}>{poll.name}</a>}
            {poll.locked && <>{poll.name}</>}
            {poll.results && <> (<a target="_blank" rel="noreferrer" href={poll.results}>Результаты</a>)</>}
            {!poll.results && poll.locked && <> <sup className="muted">недоступно</sup></>}
        </>)
}
const DepTreeElement = ({dep}) => {
    return (
        <li>
            <DepLink dep={dep}/>
            {dep.departments && (<ul>
                {dep.departments.map(d => (<DepTreeElement key={`dep_${d.id}`} dep={d}/>))}
            </ul>)}
        </li>
    )
}


export {UserLink, DepLink, DocLink, PollLink, DepTreeElement};